import React, {ComponentType} from 'react'
import store, {AppStateType} from './Redux/redux-store'
import {BrowserRouter, Link, Redirect, Route, Switch, withRouter} from 'react-router-dom'
import {connect, Provider} from 'react-redux'
import News from './components/News/News'
import Music from './components/Music/Music'
import Settings from './components/Settings/Settings'
import {library} from '@fortawesome/fontawesome-svg-core'
import {far} from '@fortawesome/free-regular-svg-icons'
import 'antd/dist/antd.css'
import './style.css'
import {LoginPage} from './components/Login/loginPage'
import {compose} from 'redux'
import {initializeApp} from './Redux/app-reducer'
import Loader from './components/common/preloader'
import {withSuspense} from './Hoc/WithSuspense'
import {Users} from './components/Users/Users'
import Layout from 'antd/lib/layout'
import {slide as Menu} from 'react-burger-menu'
import {HomeOutlined, MessageOutlined, UsergroupAddOutlined, WechatOutlined} from '@ant-design/icons'
import {AppHeader} from './components/Header/Header'
import {Tooltip} from 'antd'

const {Content, Footer} = Layout

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'))
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'))
const ChatPage = React.lazy(() => import('./pages/Chat/ChatPage'))

library.add(far)

type StatePropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = {
    initializeApp: () => void
}

const SuspendedDialogs = withSuspense(DialogsContainer)
const SuspendedProfile = withSuspense(ProfileContainer)
const SuspendedChat = withSuspense(ChatPage)

const styles = {
    bmBurgerButton: {
        color: 'white',
        position: 'fixed',
        width: '36px',
        height: '30px',
        right: '36px',
        top: '18px'
    },
    bmBurgerBars: {
        background: '#ffffff'
    },
    bmBurgerBarsHover: {
        background: '#a90000'
    },
    bmCrossButton: {
        height: '24px',
        width: '24px'
    },
    bmCross: {
        background: '#bdc3c7'
    },
    bmMenuWrap: {
        position: 'fixed',
        height: '100%',
        width: '260px'
    },
    bmMenu: {
        background: '#373a47',
        padding: '2.5em 1.5em 0',
        fontSize: '2.15em'
    },
    bmMorphShape: {
        fill: '#fc122c'
    },
    bmItemList: {
        display: 'flex',
        flexDirection: 'column',
        color: '#b8b7ad',
    },
    bmItem: {
        display: 'inline-block'
    },
    bmOverlay: {
        background: 'rgba(0, 0, 0, 0.3)'
    }
}


class App extends React.Component<StatePropsType & DispatchPropsType> {
    catchAllUnhandledErrors = (e: PromiseRejectionEvent) => {
        alert('Some error occurred')
    }

    componentDidMount() {
        this.props.initializeApp()
        window.addEventListener('unhandledrejection', this.catchAllUnhandledErrors)
    }

    componentWillUnmount() {
        window.removeEventListener('unhandledrejection', this.catchAllUnhandledErrors)
    }

    render() {
        if (!this.props.initialized) {
            return <Loader/>
        }
        return (
            <Layout className="layout" style={{minHeight: '100vh'}}>
                <AppHeader/>
                <Tooltip title={'Menu'}>
                    <div>
                    <Menu styles={styles} right>
                        <Link to="/profile">
                            <HomeOutlined style={{fontSize: 30, margin: 0}}/>Profile
                        </Link>
                        <Link to="/dialogs">
                            <MessageOutlined style={{fontSize: 30, margin: 0}}/>Messages
                        </Link>
                        <Link to="/users">
                            <UsergroupAddOutlined style={{fontSize: 30, margin: 0}}/>Users
                        </Link>
                        <Link to="/chat">
                            <WechatOutlined style={{fontSize: 30, margin: 0}}/>Chat
                        </Link>
                    </Menu>
                    </div>
                </Tooltip>
                <Content className={'content-field'}
                         style={{padding: '0 50px', height: '100%', width: '50%', margin: 'auto', marginTop: 70}}>
                    <div className="site-layout-content">
                        <Switch>
                            <Route exact path="/"
                                   render={() => <Redirect to={'/profile'}/>}/>
                            <Route path="/dialogs/:userId?"
                                   render={() => <SuspendedDialogs/>}/>
                            <Route path="/profile/:userID?"
                                   render={() => <SuspendedProfile/>}/>
                            <Route path="/users"
                                   render={() => <Users/>}/>
                            <Route path="/news"
                                   render={() => <News/>}/>
                            <Route path="/music"
                                   render={() => <Music/>}/>
                            <Route path="/settings"
                                   render={() => <Settings/>}/>
                            <Route path="/login"
                                   render={() => <LoginPage/>}/>
                            <Route path="/chat"
                                   render={() => <SuspendedChat/>}/>
                            <Route path="*"
                                   render={() => <div>404 NOT FOUND</div>}/>
                        </Switch>
                    </div>
                </Content>
                <Footer style={{textAlign: 'center'}}>FriendHook 2021 Created by Onium Web</Footer>
            </Layout>
        )
    }
}

const mapStateToProps = (state: AppStateType) => ({
    initialized: state.app.initialized
})

let AppContainer = compose<ComponentType>(
    withRouter,
    connect(mapStateToProps, {initializeApp}))(App)

const SocNetApp: React.FC = () => {
    return <BrowserRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </BrowserRouter>
}

export default SocNetApp