import React, {useEffect} from 'react'
import {Link} from 'react-router-dom'
import {Avatar, Button, Col, Layout, Popover, Row, Space, Tooltip} from 'antd/lib/'
import {MailOutlined, PoweroffOutlined, UserOutlined} from '@ant-design/icons'
import {useDispatch, useSelector} from 'react-redux'
import {selectIsAuth, selectUserLogin} from '../../Redux/Selectors/auth-selectors'
import {logout} from '../../Redux/auth-reducer'
import logo from '../../img/Logo.png'
import Title from 'antd/lib/typography/Title'
import {newMessagesCount} from '../../Redux/dialogsReducer'
import {messagesCount} from '../../Redux/Selectors/dialogs-selectors'

export type PropsType = {}

export const AppHeader: React.FC<PropsType> = (props) => {
    const {Header} = Layout
    const isAuth = useSelector(selectIsAuth)
    const login = useSelector(selectUserLogin)
    const dispatch = useDispatch()
    const newMessages = useSelector(messagesCount)

    useEffect(()=>{
        dispatch(newMessagesCount())
    },[])

    const logoutCallback = () => {
        dispatch(logout())
    }

    const content = (
        <div>
            <Link to={'/dialogs'}><p><MailOutlined /> Messages ({newMessages})</p></Link>
            <p>New Posts(2)</p>
        </div>
    )

    return (
        <Header className={'header'} style={{backgroundColor: '#373a47', position: 'fixed', width: '100%'}}>
            <Row justify="space-between">
                <Col span={16}>
                    <Space><img style={{width: 70}} src={logo} alt="Logo"/>
                        <Title level={3} style={{color: '#e7e7e7', margin: 10}}>Friends Hook</Title>
                    </Space>
                </Col>
                <Col span={6}>
                    {isAuth
                        ? <Space>
                            <Popover overlayInnerStyle={{color:'white'}} color={'#373a47'} style={{backgroundColor: '#373a47', color: 'white'}} content={content} title={login}>
                                <Avatar style={{backgroundColor: '#41b4e9',}}
                                        size={{xs: 25, sm: 30, md: 35, lg: 40, xl: 45, xxl: 47}}
                                        icon={<UserOutlined/>}/>
                            </Popover>
                            <Tooltip title={'LogOut'}><Button onClick={logoutCallback}
                                                              className={'powerOff'}><PoweroffOutlined
                                style={{fontSize: 24, color: 'white'}}/></Button></Tooltip></Space>
                        : <Button><Link to={'/login'}>Login</Link></Button>
                    }
                </Col>
                <Col span={2}>

                </Col>
            </Row>
        </Header>
    )
}