import {APIResponseType, GetMessagesAPIType, instance} from './api'
import {DialogsType} from '../Redux/dialogsReducer'
import {MessagesType} from '../types/types'

export const dialogsAPI = {
    getDialogs() {
        return instance.get<DialogsType>(`dialogs`)
            .then(res => res.data)
    },
    startMessages(userId: number) {
        return instance.put<APIResponseType>(`dialogs/${userId}`)
            .then(res => res.data)
    },
    getMessages(userId: number, page: number, count: number | null = null) {
        return instance.get<GetMessagesAPIType>(`dialogs/${userId}/messages?page=${page}&count=${count}`)
            .then(res => res.data.items)
    },
    sendMessage(userId: number, body: MessagesType) {
        return instance.post<APIResponseType>(`dialogs/${userId}/messages`, {body})
            .then(res => res.data)
    },
    deleteMessage(messageId: string) {
        return instance.delete<APIResponseType>(`dialogs/messages/${messageId}`)
            .then(res => res.data)
    },
    newMessagesCount() {
        return instance.get<APIResponseType>(`dialogs/messages/new/count`)
            .then(res => res.data)
    },
    restoreDeletedMessages(messageId:string) {
        return instance.get<APIResponseType>(`dialogs/messages/${messageId}/restore`)
            .then(res => res.data)
    },

}